.expense-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap-reverse;
      flex-wrap: wrap-reverse;
}

table {
    table-layout: fixed;
    border: 3px solid black;
  }

th, td {
    padding: 10px;
    border-bottom: 1px solid #ddd;
    word-wrap: wrap;
    max-width: 140px;
    text-align: right;
}
  
th:nth-child(1), td:nth-child(1) {
    max-width: 180px;
    text-align: left;
}

tbody tr:hover {
    background-color: #f5f5f5;
}


